import { assign } from '../../util/ObjectUtils';
import {
 REQUEST_REGISTER_PAGE, SET_USERNAME_INPUT, SET_PASSWORD_INPUT, HANDLE_REGISTER 
} from './authActionCreators';

const authReducer = (state, action) => {
  const { type, payload } = action;
  switch (type) {
    case REQUEST_REGISTER_PAGE:
    case SET_USERNAME_INPUT:
    case SET_PASSWORD_INPUT:
    case HANDLE_REGISTER:
    case 'SHOW_USER_MENU':
    case 'HIDE_USER_MENU':
      return assign(state, { ...payload });
    default:
      return assign(state);
  }
};

export default authReducer;

export const getAuthInfoSelector = (stateAuthInfo) => {
  // do some filtering of state and
  // return new state
  console.log(stateAuthInfo);
  return stateAuthInfo;
};
